module.exports = "/* eslint react/no-multi-comp: 0, react/prop-types: 0 */\n\nimport React from 'react';\nimport { Button, Toast, ToastBody, ToastHeader } from 'reactstrap';\n\nclass ToastDismissExample extends React.Component {\n  constructor(props) {\n    super(props);\n    this.state = {\n      show: false\n    };\n\n    this.toggle = this.toggle.bind(this);\n  }\n\n  toggle() {\n    this.setState({\n      show: !this.state.show\n    });\n  }\n\n  render() {\n    return (\n      <div>\n        <Button color=\"primary\" onClick={this.toggle}>{this.props.buttonLabel}</Button>\n        <br />\n        <br />\n        <Toast isOpen={this.state.show}>\n          <ToastHeader toggle={this.toggle}>Toast title</ToastHeader>\n          <ToastBody>\n            Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.\n          </ToastBody>\n        </Toast>\n      </div>\n    );\n  }\n}\n\nexport default ToastDismissExample;\n"