module.exports = "/* eslint react/no-multi-comp: 0, react/prop-types: 0 */\n\nimport React from 'react';\nimport { Button, Popover, PopoverHeader, PopoverBody } from 'reactstrap';\n\nclass PopoverItem extends React.Component {\n  constructor(props) {\n    super(props);\n\n    this.toggle = this.toggle.bind(this);\n    this.state = {\n      popoverOpen: false\n    };\n  }\n\n  toggle() {\n    this.setState({\n      popoverOpen: !this.state.popoverOpen\n    });\n  }\n\n  render() {\n    return (\n      <span>\n        <Button className=\"mr-1\" color=\"secondary\" id={'Popover-' + this.props.id} type=\"button\">\n          {this.props.item.text}\n        </Button>\n        <Popover placement={this.props.item.placement} isOpen={this.state.popoverOpen} target={'Popover-' + this.props.id} toggle={this.toggle}>\n          <PopoverHeader>Popover Title</PopoverHeader>\n          <PopoverBody>Sed posuere consectetur est at lobortis. Aenean eu leo quam. Pellentesque ornare sem lacinia quam venenatis vestibulum.</PopoverBody>\n        </Popover>\n      </span>\n    );\n  }\n}\n\nclass PopoverExampleMulti extends React.Component {\n  constructor(props) {\n    super(props);\n\n    this.state = {\n      popovers: [\n        {\n          placement: 'top',\n          text: 'Top'\n        },\n        {\n          placement: 'bottom',\n          text: 'Bottom'\n        },\n        {\n          placement: 'left',\n          text: 'Left'\n        },\n        {\n          placement: 'right',\n          text: 'Right'\n        }\n      ]\n    };\n  }\n\n  render() {\n    return (\n      <div>\n        { this.state.popovers.map((popover, i) => {\n          return <PopoverItem key={i} item={popover} id={i} />;\n        })}\n      </div>\n    );\n  }\n}\n\nexport default PopoverExampleMulti;\n"