module.exports = "import React from 'react';\nimport { Pagination, PaginationItem, PaginationLink } from 'reactstrap';\n\nexport default class Example extends React.Component {\n  render() {\n    return (\n      <Pagination size=\"sm\" aria-label=\"Page navigation example\">\n      <PaginationItem>\n          <PaginationLink first href=\"#\" />\n        </PaginationItem>\n        <PaginationItem>\n          <PaginationLink previous href=\"#\" />\n        </PaginationItem>\n        <PaginationItem>\n          <PaginationLink href=\"#\">\n            1\n          </PaginationLink>\n        </PaginationItem>\n        <PaginationItem>\n          <PaginationLink href=\"#\">\n            2\n          </PaginationLink>\n        </PaginationItem>\n        <PaginationItem>\n          <PaginationLink href=\"#\">\n            3\n          </PaginationLink>\n        </PaginationItem>\n        <PaginationItem>\n          <PaginationLink next href=\"#\" />\n        </PaginationItem>\n        <PaginationItem>\n          <PaginationLink last href=\"#\" />\n        </PaginationItem>\n      </Pagination>\n    );\n  }\n}\n"